	module.exports = {"data":{"markdownRemark":{"html":"<p>同人誌から抜け出して久々に商業誌の話。ちょっと探してる漫画があるんだけど、なかなか見つからない。以前持ってて一度売ってしまった本なんだが、たまにまた読みたくなるんだよなぁ。ちょっとパロディネタに使いたくて(←</p>\n<ul>\n<li>ミルククローゼット / 富沢ひとし 全1～4巻 再版予定無し</li>\n<li>プロペラ天国 / 富沢ひとし 全1巻 再版予定無し\n一度売ってしまった単行本は二度と手に入らないのかなぁ 'Ａ｀ そもそも売ったのはつまんないから、とかじゃなくて生活費のためやむを得ず、だったのだが。(だから自宅は極端に蔵書数が少ない)\nBOOK OFFも二店巡回したが空振り。もうちょっと探してみるかのぅ… orz</li>\n</ul>\n<h2>昼食に行く度耳に入るあの曲</h2>\n<p>ネット中探しまくってやっとみっけた。</p>\n<ul>\n<li><a href=\"http://www.muzie.co.jp/artist/a048912/\">musie 清田愛未</a>\n会社の昼休憩で飯食いに川越のクレアモールを徘徊してるんだけど、その近くにある巨大スクリーン『小江戸ライブビジョン』。そこから流れてくる曲で聞く度にひとつだけ「おっ」って思う曲が。プラネタリウム番組CMのBGMとして流れるこの『星霜』って曲。\n個人的にこの曲いいなぁ。野外スピーカーからだとボーカルがRIKKIっぽくも聞こえたけど、自宅で聞くとやっぱ全然違う。密かにこう言う聞いてて悲しくなるような曲作ってみたいが、(少なくともオリジナルとしては)テクノ系しか作ったことないウチにとってはまるで万里の長城並みの難易度やなー orz</li>\n</ul>","frontmatter":{"cover":null,"date":"2007-03-22T06:06:00+09:00","strDate":"2007/3/21","title":"♪みみ みみ おっきなみみ(WANTED)"}}},"pathContext":{"path":"/ja/blog/2007/03/22-♪みみ-みみ-おっきなみみwanted/","langKey":"ja"}}

